@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css?family=Hi+Melody&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nova+Cut&display=swap);
@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
#header-container {
  padding: 0;
}
#company-language-row {
  border-bottom: 2px solid #e0e0e0;
  padding: 10px 0;
}
#company-title {
  line-height: 2.4;
  color: #c0392b;
  font-family: "Orbitron", sans-serif;
}
#language-container {
  text-align: right;
}
#language-button {
  background-color: #e0e0e0;
  border-color: black;
  color: black;
  width: 200px;
}

.navbar-row {
  margin-top: 10px;
}

#show-title {
  margin-top: 15px;
  color: #bb201e;
  font-family: "Orbitron", sans-serif;
  text-shadow: -1px -1px 0 grey, 1px -1px 0 grey, -1px 1px 0 grey,
    1px 1px 0 grey;
}

@media only screen and (max-width: 560px) {
  #company-title {
    font-size: 12px;
  }
  #show-title h1 {
    font-size: 1.9rem;
  }
  .navbar-row {
    justify-content: center;
  }
}

#inspiration-container {
  margin-top: 15px;
}
h2 {
  margin-bottom: 2.5rem;
  border-bottom: 2px solid #e0e0e0;
  border-top: 2px solid #e0e0e0;
  padding: 10px;
  margin-right: -28px;
  margin-left: -28px;
  padding-left: 25px;
  font-family: "Nova Cut", cursive;
}
#logo-container {
  margin: auto;
}

.description-text {
  margin-top: 50px !important;
}
.player-wrapper {
  position: relative;
  padding-top: 56.25%; /* 720 / 1280 = 0.5625 */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

p {
  font-family: "Hi Melody", cursive;
  font-size: 25px;
}

@media only screen and (max-width: 560px) {
  h2 {
    font-size: 1.5rem;
  }
  p {
    font-size: 20px;
  }
  #description-container {
    margin-top: 20px;
  }
}

#gallery-container {
  margin-top: 25px;
}
h2 {
  margin-bottom: 2.5rem;
}

ul {
  padding-left: 25px;
}

.carousel {
  width: 80%;
  margin: auto;
}

.location-column {
  margin: auto;
}

@media only screen and (max-width: 560px) {
  .carousel {
    width: 100%;
  }
  .location-column {
    width: 50%;
    margin-top: 20px;
  }
}

#episodes-container {
  margin-top: 25px;
}
h2 {
  margin-bottom: 2.5rem;
}
h5 {
  font-family: "Abel", sans-serif;
}
li {
  list-style: none;
}
.ratings {
  text-align: center;
}
.fa-star {
  color: gold;
  margin-left: 10px;
}

.btn-outline-secondary {
  margin-left: 25px;
}

@media only screen and (max-width: 560px) {
  #episodes-container li h5 {
    font-size: 12px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

#root {
  background-color: #424242;
  color: #bdbdbd;
  /* height: 100vh; */
}

